import { ScoreSuccessEnum } from "../types/SystemScoreSuccessEnum";
import { createTheme } from '@material-ui/core/styles';

export const scoreToColorConverter = (scoreSuccess: ScoreSuccessEnum | undefined):string => {
    const theme = createTheme();
    if (typeof scoreSuccess === undefined) {
        return theme.palette.grey[500];
    }
    switch (scoreSuccess) {
        case ScoreSuccessEnum.Success:
          return theme.palette.success.light;
        case ScoreSuccessEnum.Failure:
          return theme.palette.error.light;
        case ScoreSuccessEnum.Partial:
          return theme.palette.warning.light;
        default:
          return theme.palette.grey[500];
      }
}
